<template>
  <div class="container" v-loading.fullscreen.lock="fullscreenLoading">
    <div class="card" v-for="(item,index) in this.appList.data">
      <div class="item1">
        <div class="Sername">{{item.serviceItemName}}</div>
        <div class="Serdate">{{item.reservationServiceTime}}</div>
      </div>
      <div class="item2">
        <span>{{item.doctorName}}</span>
      </div>
      <div class="item3">
      </div>
    </div>
    <!--<div class="null" id="null">
      <img src="../assets/img/wushuju.png" >
      <p></p>
    </div>-->
    <null v-show=this.flag></null>
    <div style="height: 40px"></div>

    <!--<div class="header" id="header">
      <span @click="back"><img src="../assets/img/back.png"></span>
      <span @click="close"><img src="../assets/img/close.png"></span>
      <span @click="advance"><img src="../assets/img/advance.png"></span>
    </div>-->
  </div>
</template>

<script>
  import Null from '../components/null'
    export default {
        name: "Myappointment",
      data() {
        return {
          flag: false,
          rep: '',
          menuList: '',
          Histories: '',
          appList: '',
          msg: '',
          fullscreenLoading: false
        }
      },
      computed: {
        api_url() {
          return this.$store.state.api_url
        },
        cusId() {
          return this.$store.state.cusId
        },
      },
      components: {
        null: Null
      },
      /*mounted(){
        if(this.serList.data.length===0){
          this.flag=true;
        }
      },*/
      created() {
        this.post();
      },
      methods: {
        open4(info) {
          this.$message.error(info);
        },
        /**
         *
         * 
         *
         * */

        getlist() {
          // document.getElementById('load').style.display='block';
          // console.log(this.$route.params.cusId)
          this.openFullScreen();
          this.axios.post(this.api_url + 'Reservation/getReservation/'+this.$route.params.cusId)
          //
            .then(response => {
              if (response.data.code === "0002") {
                this.post();
                this.getlist();
              }if(response.data.code === "0001"){
                this.open4(response.data.msg);
                this.$router.push({path:'/appSuccess/'+this.$route.params.cusId})
              } else {
                this.appList = response.data;
                this.fullscreenLoading=false;
                // console.log(this.appList)
                if(this.appList.data.length===0){
                  this.flag=true
                }
              }

            })
            //
            .catch(error => {
              console.log(error)
            });

        },
        openFullScreen() {
          this.fullscreenLoading = true;
          setTimeout(() => {
            this.fullscreenLoading = false;
          }, 30000);
        },
        post() {
          this.axios.post(this.api_url + 'security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
            data: {
              key: "",
              requestTime: ""
            }
          })
          //
            .then(response => {
              // console.log(response.data);
              this.msg = response.data;
              this.getlist();
            })
            //
            .catch(error => {
              console.log(error)
            })
        }
      }
    }
</script>

<style scoped>
  .card {
    margin: 10px;
    display: flex;
    border: .5px solid #6296ca;
    flex-wrap: wrap;
    border-radius: 6px;
    background-color: white;
    font-size: 12px;
  }

  .item1 {
    flex: 1;
    /*background-color: cornflowerblue;*/
    padding-left: 10px;
  }

  .item1 .Sername {
    display: flex;
    justify-content: flex-start;
    margin: 10px 0;
    font-size: 16px;
    font-weight: 600;
  }

  .item1 .Serdate {
    display: flex;
    justify-content: flex-start;

  }

  .item2 span {
    color: #6495ed;

  }

  .item2 {
    position: absolute;
    right: 10px;
    margin-top: 42px;
    justify-content: flex-end;
    align-items: flex-end;
    padding-right: 10px;
  }

  @media (max-width: 340px) {
    .card{
      display: block;
    }
    .item1 {
      display: block;
      line-height: normal;
      /*background-color: cornflowerblue;*/
    }
    .item2 {
      position: static;
      display: inline-block;
      padding-left: 10px;
      margin-top: 0;
    }
  }
  .item3 {
    margin-top: 10px;
    border-radius: 5px;
    display: flex;
    min-height: 10px;
    min-width: 100%;
    background-color: #6495ed;
  }
</style>



