<template>
  <div v-loading.fullscreen.lock="fullscreenLoading">

    <div class="re-container">

      <div class="re-line">
        <img class="icons" src="../assets/img/icon-account1.png">
        <input type="text" v-model="userName" @keyup="checked(userName,checkedMsg.userName)" @change="checked(userName,checkedMsg.userName)" placeholder=""><span
        class="checked">{{this.checkedMsg.userName.msg}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-account1.png">
        <input type="text" v-model="cusUserName" @keyup="checked(cusUserName,checkedMsg.cusUserName)" @change="checked(cusUserName,checkedMsg.cusUserName)" placeholder=""><span
        class="checked">{{this.checkedMsg.cusUserName.msg}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-brith.png">
        <el-date-picker
          class="input-class"
          v-model="cusBirth"
          prefix-icon="none"
          @change="checked(cusBirth,checkedMsg.cusBirth)"
          placeholder="">
        </el-date-picker>
        <span class="checked">{{this.checkedMsg.cusBirth.msg}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-sex.png">
        <el-select v-model="cusSex" class="input-class" @change="checked(cusSex,checkedMsg.cusSex)" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <span class="checked">{{this.checkedMsg.cusSex.msg}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-phone.png">
        <input type="text" v-model="cusMobile" @keyup="checked(cusMobile,checkedMsg.cusMobile)"
               placeholder=""><span class="checked">{{this.checkedMsg.cusMobile.msg}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-yanzhengma.png">
        <input type="text" v-model="cusPhoneCode" placeholder=""><input class="span" type="button" id="btn" value="" @click="condsend()" onclick="settime(this)" />
        <!--<span @click="settime()">{{this.codemsg}}</span>-->
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-secret.png">
        <input type="password" v-model="userPwd" @keyup="checked(userPwd,checkedMsg.userPwd)" placeholder=""><span
        class="checked">{{this.checkedMsg.userPwd.msg}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-secret.png">
        <input type="password" v-model="userPwd1" @keyup="passcheck()" placeholder=""><span class="checked">{{this.checkedMsg.userPwd1.msg}}</span>
      </div>
      <div class="btn" @click="sumit">
        <button>&nbsp &nbsp&nbsp &nbsp&nbsp &nbsp</button>
      </div>
      <div class="smitinfo">{{this.smitInfo}}</div>
      <div class="re-more">
        *
        <a href=""></a>
      </div>
    </div>
    <div style="height:70px"></div>
  </div>
</template>

<script>
  var countdown=60;
  var codeflag=true;
  window.settime=function (val) {

      if (countdown == 0) {
        val.removeAttribute("disabled");
        val.value="";
        countdown = 60;
      } else if(!codeflag){
        val.removeAttribute("disabled");
        val.value="";
        countdown = 60;
        codeflag=true;
        return 0;
      } else {
        val.setAttribute("disabled", true);
        val.value="(" + countdown + ")";
        countdown--;
        setTimeout(function() {
          settime(val)
        },1000)
      }

  }
  var h = document.body.scrollHeight;
  window.onresize = function () {
    if (document.body.scrollHeight < h) {
      document.getElementById('bttombtn').style.display = 'none';
    } else {
      document.getElementById('bttombtn').style.display = 'block';
    }
  };
  export default {
    name: "Register",
    data() {
      return {

        fullscreenLoading: false,
        checkedMsg: {
          cusUserName: {msg: '*', flag: false, rule: /^.+$/, msgError: '*'},
          userName: {msg: '*', flag: false, rule: /^[0-9a-zA-Z]*$/, msgError: '*'},
          cusBirth: {msg: '*', flag: false, rule: /^.+$/, msgError: ''},
          cusMobile: {msg: '*', flag: false, rule: /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/, msgError: ''},
          cusPhoneCode: {msg: '* ', flag: false, rule: /^.+$/, msgError: '*'},
          cusSex: {msg: '*', flag: false, rule: /^.+$/, msgError: ''},
          openid: {msg: '*', flag: false, rule: '', msgError: ''},
          userPwd: {msg: '*', flag: false, rule: /^.{6,16}$/, msgError: '6-16'},
          userPwd1: {msg: '*', flag: false,  msgError: ''},
        },
        smitInfo: '',
        cusBirth: "",
        cusMobile: "",
        cusPhoneCode: "",
        cusUserName:'',
        cusSex: "",
        openid: this.$route.params.openid,
        userName: "",
        userPwd: "",
        userPwd1: "",
        options: [{
          value: '0',
          label: ''
        }, {
          value: '1',
          label: ''
        }],
      }
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
    },
    created() {
      this.post();
    },
    methods: {
      passcheck(){
        if(this.userPwd===this.userPwd1){
          this.checkedMsg.userPwd1.msg=''
          this.checkedMsg.userPwd1.flag=true
        }else {
          this.checkedMsg.userPwd1.msg=this.checkedMsg.userPwd1.msgError
        }
      },
      open4(info) {
        this.$message.error(info);
      },
      open2(info) {
        this.$message({
          message: info,
          type: 'success'
        });
      },
      openFullScreen() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 2000);
      },
      condsend(){
        console.log(this.msg.data)
        this.axios.post(this.api_url + 'register/getPhoneCode/'+this.cusMobile+'/'+0, {}, {
          headers: {"Authorization": this.msg.data},
        })
        //
          .then(response => {
            if(response.data.code=='0001'){
              this.open4(response.data.msg)
            }

            console.log('')

          })
          //
          .catch(error => {
            this.open4('');
            codeflag=false;
            console.log(error)
          });
      },
      post() {
        this.axios.post(this.api_url + 'security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      checked(pass, obj) {
        if (pass != "" && !obj.rule.test(pass)) {
          obj.msg = obj.msgError;
          return false;
        } else if (pass == "") {
          obj.msg = '*';
          obj.flag = false;
        } else {
          obj.msg = '';
          obj.flag = true;
        }
      },
      regist() {
        this.post();
        console.log(this.msg.data);
        this.axios.post(this.api_url + 'register/registerUser', {
          "cusBirth": this.cusBirth,
          "cusMobile": this.cusMobile,
          "cusPhoneCode": this.cusPhoneCode,
          "cusSex": this.cusSex,
          "cusUserName": this.userName,
          "openid": this.openid,
          "userName": this.cusUserName,
          "userPwd": this.userPwd,
        }, {
          headers: {"Authorization": this.msg.data},

        })
        //
          .then(response => {
            if (response.data.code === "0002") {
              this.post();
              this.regist();

            } else if (response.data.code === "0001") {
              this.open4(response.data.msg)
            }else {
              this.open2(response.data.msg)
              console.log('');
              this.$router.push({path:'/Login/'+this.cusMobile+'/'+this.openid});
            }

          })
          //
          .catch(error => {
            console.log(error)
          });
      },
      sumit() {
        let obj = this.checkedMsg;
        let flag = obj.userName.flag && obj.cusBirth.flag && obj.cusMobile.flag && obj.cusSex.flag && obj.userPwd.flag&& obj.userPwd1.flag;
        console.log(flag);
        console.log(this.checkedMsg.userPwd);
        if (flag) {
          this.smitInfo = '';
          this.openFullScreen()
          this.regist();
        } else {
          this.smitInfo = ''
        }
      }
    }
  }
</script>

<style lang="less">
  #btn{
    font-size: 14px;
    background-color: rgba(255,255,255,0);
  }
  input::-webkit-input-placeholder {

    /* placeholder  */
    font-size: 14px;
    color: #c0c4cc;
  }
  .el-date-editor.input-class.el-input.el-input--prefix.el-input--suffix.el-date-editor--date input{
    padding-left: 0px;
  }
  .el-input.el-input--suffix input{
    padding-left: 0px;
    padding-right: 0px;
  }
  /*.el-input__icon.el-icon-date{
    display: none;
  }*/
  .smitinfo {
    position: absolute;
    color: red;
    left: 0;
    right: 0;
    text-align: center;
  }

  .checked {
    position: absolute;
    right: 20px;
    margin-top: 20px;
    color: red !important;
  }

  .input-class {
    .el-input__inner {
      border: 0;
      background-color: #f7f8f8;
    }
  }
  input{
    background-color: #f7f8f8;
  }
  .re-bg-bottom {
    position: fixed;
    width: 100%;
    bottom: 0;
  }

  .re-container {
    height: 100vh;
    width: 100%;
    text-align: center;

  }

  .re-line {
    text-align: left;
    padding: 5px 0;
    margin: 10px 20px;
    border-bottom: 1px solid #cccccc;
  }

  .re-line .span {
    position: absolute;
    right: 20px;
    background-color: white;
    color: rgb(123, 174, 245);
    font-size: 16px;
  }

  .icons {
    width: 25px;
    margin: 0 10px;
    position: relative;
    bottom: -8px;
  }

  input {
    margin-left: 10px;
    border: 0;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
  }

  input:focus {
    outline: none;
  }

  .btn {
    margin-top: 6vh;
    text-align: center;
  }

  .btn button {

    text-align: center;

    font-size: 18px;
    width: 60%;
    background: linear-gradient(to top, rgb(123, 174, 245), rgb(58, 142, 234));
    border: 0;
    border-radius: 25px;
    height: 45px;
    color: #ffffff;
  }

  .btn button:active {
    border: 2px solid #ffffff;
    border-radius: 25px;
    font-size: 17px;
  }

  .btn button:hover {
    outline: none;
  }

  .re-more {
    margin-top: 2vh;
    color: #cccccc
  }

  .re-more a {
    font-size: 16px;
    margin: 0 6px;
    text-decoration: none;
    color: rgb(123, 174, 245)
  }

  .re-more a:hover {
    text-decoration: none;
  }
</style>




