<template>
  <div class="container" v-loading.fullscreen.lock="fullscreenLoading">
    <div class="rep-nav-bar">
      <el-select v-model="value" placeholder="" class="input-class">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>

    <div class="report">
      <transition-group tag="div">
        <div class="card" @click="openRep(item)" v-for="(item,key,index) in this.rep.data" v-bind:key="key"
             v-show="item.repotrClass===value||value===1">
          <div class="icons">
            <img class="iconImg" :src=item.img>
          </div>
          <div class="rep-name">{{item.name}}</div>
          <div class="date">
            <p>{{item.year}}</p>
            <p>{{item.date}}</p>
          </div>
        </div>
      </transition-group>

      <!-- <table >
         <tbody>
         &lt;!&ndash;<transition-group appear tag="tr">&ndash;&gt;
         <tr v-for="(item,index) in this.rep.data" v-bind:key="item" v-show="item.color===value||value===1">
           <td @click="openRep(item.url)"><img class="iconImg" :src=item.img>
           </td>
           <td class="title" @click="openRep(item.url)">{{item.name}}</td>
           <td class="Border" style="border-color: #4584E5" @click="openRep(item.url)">
             <p>{{item.year}}</p>
             <p>{{item.date}}</p>
           </td>
         </tr>
         &lt;!&ndash;</transition-group>&ndash;&gt;
         </tbody>
       </table>-->
    </div>

    <div class="header" id="header">
      <span @click="back"><img src="../assets/img/back.png"></span>
      <span @click="close"><img src="../assets/img/close.png"></span>
      <span @click="advance"><img src="../assets/img/advance.png"></span>
    </div>
    <null v-show=this.flag></null>
    <div style="height: 40px"></div>
  </div>
</template>

<script>
  import wx from 'weixin-js-sdk'
  import Null from './null'

  export default {
    name: "HealthReport",
    data() {
      return {
        fullscreenLoading: false,
        flag: false,
        msg: '',
        rep: JSON.parse(sessionStorage.getItem("Myrep")),
        size: 'mini',
        options: [
          {
            value: 1,
            label: ''
          }, {
            value: "zy",
            label: ''
          }, {
            value: "xl",
            label: ''
          }, {
            value: "fx",
            label: ''
          },
          {
            value: "tsn",
            label: ''
          },
          {
            value: "yd",
            label: ''
          },
          {
            value: "sw",
            label: ''
          }],
        value: 1
      }
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
      cusId() {
        return this.$route.params.cusId
      },

    },
    components: {
      null: Null
    },

    updated() {
      if (this.rep.data.length === 0) {
        this.flag = true;
      }
      let arr1 = new Array(this.rep.data.length);
      for (let i = 0; i < arr1.length; i++) {
        arr1[i] = i;
      }

      for (let i = 0; i < arr1.length; i++) {
        let Id = arr1[i];
        document.getElementsByClassName('card')[Id].style.borderColor = this.rep.data[i].color;
      }

    },
    created() {
      this.post();
      this.getdata();
    },
    methods: {
      openFullScreen() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 6000);
      },
      post() {
        this.axios.post(this.api_url + 'security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      close() {
        wx.miniProgram.navigateBack({
          delta: 2
        });
      },
      advance() {
        this.$router.go(1);
      },
      back() {
        this.$router.go(-1);
        sessionStorage.removeItem("Myrep");
        this.$destroy('HealthReport')
      },
      openRep(rep) {
        this.$store.commit('repurl', rep.url);
        console.log(this.$store.state.repurl);
        if (rep.repotrType === "pdf") {
          this.$router.push({path: '/reportCon'});
        }else if(rep.repotrType === "png"){
          this.$router.push({path: '/reportConimg'});
        }

      },
      /**
       *
       * 
       *
       * */
      getdata() {

        this.openFullScreen();
        this.axios.get(this.api_url + 'report/getPerotList/' + this.$route.params.cusId, {headers: {'Authorization': this.msg.data}})
        //
          .then(response => {
            console.log(response.data);
            this.rep = response.data;

            if (this.rep.code === '0002') {
              this.post();
              this.getdata();
            } else {
              this.$store.commit('Myrep', this.rep);
              sessionStorage.setItem("Myrep", JSON.stringify(this.rep));
              this.fullscreenLoading = false;
            }

          })
          //
          .catch(error => {
            console.log(error)
          });

      },

    }
  }
</script>
<style scoped>
  .rep-nav-bar {
    width: 100%;
    background-color: white;
    text-align: center;
    box-shadow: 0 2px 10px #cccccc;
  }

  .v-enter,
  .v-leave-to {
    transform: translateY(100px);
    opacity: 0;
  }

  .v-leave-active,
  .v-enter-active {
    transition: all 0.5s ease;
  }

  .v-leave-active {
    position: absolute;
  }

  .report {
    margin: 10px;
    background-color: #f7f8f8;
  }

  .report .card {
    background-color: white;
    height: 80px;
    display: flex;
    margin: 10px 0;
    border-radius: 5px;
    border-right: 10px solid;
    box-shadow: 2px 2px 10px #cccccc;
  }

  .report .card .icons {
    flex: 0 1 50px;
    padding: 20px 5px 0 10px;
  }

  .report .card .icons img {
    width: 40px;
  }

  .report .card .rep-name {
    flex: 1;
    font-size: 16px;
    line-height: 80px;

  }

  .report .card .date {
    padding-top: 20px;
    line-height: 20px;
    flex: 0 1 70px;
  }
</style>
<style lang="less">
  .input-class {
    .el-input__inner {
      border: 0;
      text-align: center;
      width: 170px;
      margin-left: 0;
    }
  }
</style>



