<template>
  <div class="container">
    <div class="info">
      <div class="icon">
        <img src="../assets/img/appointment/yuyue.png" alt="">
      </div>
      <div></div>
      <p>24h</p>
      <p></p>
    </div>
    <div class="item" @click="goMyappointment">
      <span></span>
      <div class="img">
        <img src="../assets/img/advance.png" alt="">
      </div>
    </div>
    <div class="item" @click="goappointment">
      <span></span>
      <div class="img">
        <img src="../assets/img/advance.png" alt="">
      </div>
    </div>
    <div class="item" @click="close">
      <span></span>
      <div class="img">
        <img src="../assets/img/advance.png" alt="">
      </div>
    </div>
  </div>
</template>

<script>
  import wx from 'weixin-js-sdk'
  export default {
    name: "appSuccess",
    methods:{

      close(){
        wx.miniProgram.navigateBack({
          delta: 2
        });
      },
      goMyappointment(){
        this.$router.push({path:'/Myappointment/'+this.$route.params.cusId});
      },
      goappointment(){
        this.$router.push({path:'/appointment/'+this.$route.params.cusId});
      }
    }
  }
</script>

<style scoped>
  .icon {
    margin-top: 10vh;
    text-align: center;
  }

  .icon img {
    width: 15vh;
  }

  .info {
    text-align: center;
  }

  .info :nth-child(2) {
    margin: 2vh;
    font-size: 18px;
  }

  .info p {
    color: #bbbbbb;
    font-weight: 500;
  }

  .item:nth-child(2) {
    margin-top: 10vh;
    border-top: 1px solid #cccccc;
  }

  .item {
    background-color: white;
    border-bottom: 1px solid #cccccc;
    width: 100%;
    display: flex;
  }

  .item .img {
    flex: 0 1 40px;
    text-align: right;
    padding: 0 2vh;
  }

  .item span {
    flex: 1;
    line-height: 40px;
    padding-left: 3vh;

  }

  .item .img img {
    width: 12px;
    opacity: .8;
    height: 25px;
    margin: 1vh;
  }
</style>



