<template>
  <div class="container">
    <div style="margin: 10px">
      <table class="self-card">
        <tr>
          <td>
            <span></span>
            <img :src=this.msg.data.headImg width="80px">
          </td>
          <td class="ifo">
            <p>{{this.msg.data.name ||''}}</p>
            <p>{{this.msg.data.sex }}</p>
            <p>{{this.msg.data.age }}</p>
          </td>
        </tr>
      </table>
    </div>

    <div class="tab"></div>
    <div v-for="(item,index) in this.msg.data.typeList">
      <div class="list" @click="gethis(item.id)">
        <div class="icon"></div>
        <span>{{item.name}}</span></div>
    </div>
    <null v-show="this.flag"></null>
    <div style="height: 40px"></div>
    <!--<div v-for="(item,index) in msg">
      <div class="title" @click="details(index)">{{item.title}}</div>
    </div>-->
  </div>
</template>

<script>
  import Null from './null'
  export default {
    name: "Menu",
    data() {
      return {
        rep: '',
        menuList: '',
        Histories: '',
        fullscreenLoading: false,
        flag:false,
        address: '',
        cusid: JSON.parse(sessionStorage.getItem("cusid")),

      }
    },
    components:{
      null:Null
    },
    updated() {
      this.color();
    },

    computed: {
      api_url() {
        return this.$store.state.api_url
      },
      cusId() {
        return this.$store.state.cusId
      },
      msg() {
        return this.$store.state.Myifo
      }

    },
    created() {
     this.post();
    },
    methods: {
      color(){
        if(this.msg.data.typeList){
          if(this.msg.data.typeList.length===0){
            this.flag=true
          }else {
            /*icon*/
            let arr1 = new Array(this.msg.data.typeList.length);
            for (let i = 0; i < arr1.length; i++) {
              arr1[i] = i;
            }

            for (let i = 0; i < arr1.length; i++) {
              let Id = arr1[i];

              if (Id % 4 === 0) {
                document.getElementsByClassName('icon')[Id].style.backgroundColor = 'rgb(144, 198, 255)'
              } else if (Id % 4 === 1) {
                document.getElementsByClassName('icon')[Id].style.backgroundColor = 'rgb(255, 167, 131)'

              } else if (Id % 4 === 2) {
                document.getElementsByClassName('icon')[Id].style.backgroundColor = 'rgb(119, 214, 223)'

              } else if (Id % 4 === 3) {
                document.getElementsByClassName('icon')[Id].style.backgroundColor = 'rgb(255, 186, 105)'

              }

            }
          }
        }else {
          this.flag=true;
        }

      },
      openFullScreen() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 30000);
      },
      post() {
        this.axios.post(this.api_url + 'security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //
          .then(response => {
            this.address = response.data;
            this.getmenu()
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * 
       *
       * */
      getmenu() {

        this.openFullScreen();
        this.axios.post(this.api_url + 'tjZhufuhealth/getHealthIndex/' + this.$route.params.cusId)
        //
          .then(response => {
            console.log(response.data);
            this.menuList = response.data;

            if (this.rep.code === "0002") {
              this.post();
              this.getmenu();
            } else {
              sessionStorage.setItem("cusid", JSON.stringify(this.$route.params.cusId));
              this.$store.commit('Myifo',this.menuList);
              console.log('$store:'+this.$store.state.Myifo);

            }


          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * 
       *
       **/
      gethis(id) {
        console.log(this.address.data + id + this.cusid);
        this.axios.post(this.api_url + 'tjZhufuhealth/selectBycheck/' + this.cusid + '/' + id,
          {
            data: {1: 1},
          },
          {
            headers: {
              'Authorization': this.address.data
            }
          })
        //
          .then(response => {
            console.log(response.data)
            this.Histories = response.data;
            if (this.Histories.code === "0002") {
              this.post();
              this.gethis();
            } else {
              this.$router.push({path: '/Histories'});
              this.$store.commit('Histories', this.Histories)
            }
          })
          //
          .catch(error => {
            console.log(error)
          })
      }

    }
  }
</script>

<style scoped>
  .self-card {
    background-color: #ffffff;
    text-align: left;
    border: 1px solid #cccccc;
    box-shadow: 2px 2px 5px #cccccc;
    border-radius:10px ;
    width: 100%;
  }

  .self-card tr td {
    padding: 10px;
  }

  .self-card span {
    font-size: 18px;
    display: block;
    margin-bottom: 5px;
  }


  .self-card p {
    margin-left: 20px;
    font-size: 14px;
  }

  .self-card .ifo {
    width: 80%;
    /*background-color: antiquewhite;*/
  }

  .tab {
    background-color: #ffffff;
    height: 50px;
    font-size: 18px;
    text-align: center;
    line-height: 50px;
    border-bottom: 1px solid rgba(100,100,100,.2);
    border-top: 1px solid rgba(100,100,100,.2);
  }

  .list {
    background-color: #ffffff;
    text-align: left;
    height: 50px;
    font-size: 17px;
    line-height: 50px;
    border-bottom: 1px solid rgba(100,100,100,.2);
  }

  .list span {
    margin-left: 20px;
    font-weight: 600;
  }

  .icon {
    display: inline-block;
    min-height: 30px;
    min-width: 10px;
    border-radius: 5px;
    margin-left: 10px;
    position: relative;
    top: 10px;

  }
</style>



