<template>

  <div id="app">
    <div class="header" id="header">
      <span @click="back"><img src="./assets/img/back.png"></span>
      <span @click="close"><img src="./assets/img/close.png"></span>
      <span @click="advance"><img src="./assets/img/advance.png"></span>
    </div>
    <keep-alive>
      <transition mode="out-in">
        <router-view/>
      </transition>
    </keep-alive>
    <div style="height: 60px;clear: both"></div>
  </div>
</template>

<script>
  import wx from 'weixin-js-sdk'

  var h = document.body.scrollHeight;
  window.onresize = function () {
    if (document.body.scrollHeight < h) {
      document.getElementById('header').style.display = 'none';
    } else {
      document.getElementById('header').style.display = 'block';
    }
  };
  export default {
    name: 'App',
    methods: {
      close() {
        wx.miniProgram.navigateBack({
          delta: 2
        });
      },
      back() {
        this.$router.go(-1);
      },
      advance() {
        this.$router.go(1);
      }
    }

  }
</script>

<style>


</style>




