<template>
  <div class="block" v-loading.fullscreen.lock="fullscreenLoading">
    <div class="icon">
      <img class="icon" src="../assets/img/appointment/order.png" alt="">
      <p></p>
    </div>
    <el-row>
      <el-col :span="4">
        <div class="title"><img src="../assets/img/appointment/item.png" alt=""></div>
      </el-col>
      <el-col :span="20">
        <el-select v-model="servalue" placeholder="" @change="checked(servalue,checkedMsg.servalue)">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <span class="checked">{{this.checkedMsg.servalue.msg}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4">
        <div class="title"><img src="../assets/img/appointment/doctor (2).png" alt=""></div>
      </el-col>
      <el-col :span="20">
        <el-select v-model="docvalue" placeholder="" @change="checked(docvalue,checkedMsg.docvalue)">
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <span class="checked">{{this.checkedMsg.docvalue.msg}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4">
        <div class="title"><img src="../assets/img/appointment/date.png" alt=""></div>
      </el-col>
      <el-col :span="20">
        <el-date-picker
          @change="checked(datevalue,checkedMsg.datevalue)"
          v-model="datevalue"
          type="date"
          prefix-icon="none"
          :picker-options="pickerOptions"
          placeholder="">
        </el-date-picker>
        <span class="checked">{{this.checkedMsg.datevalue.msg}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4">
        <div class="title"><img src="../assets/img/appointment/remark.png" alt=""></div>
      </el-col>
      <el-col :span="20">
        <textarea v-model="desc" placeholder=""></textarea>
      </el-col>
    </el-row>
    <el-col align="center">
      <el-button type="primary" class="" @click="sumit"></el-button>
      <div class="smitinfo">{{this.smitInfo}}</div>
    </el-col>
    <!-- <el-button  @click="test()">aaa</el-button>-->

  </div>
</template>

<script>
  export default {
    name: "appointment",
    data() {
      return {
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          },
        },
        fullscreenLoading: false,
        msg: '',
        docmsg: '',
        itemmsg: '',
        desc: "",
        options1: [],
        docvalue: '',
        servalue: '',
        datevalue: '',
        options: [],
        smitInfo: '',
        checkedMsg: {
          docvalue: {msg: '* ', flag: false, rule: /^.+$/, msgError: '*'},
          servalue: {msg: '* ', flag: false, rule: /^.+$/, msgError: '*'},
          datevalue: {msg: '* ', flag: false, rule: /^.+$/, msgError: '*'}
        },
      };
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
    },
    mounted() {
      this.post();
    },
    methods: {
      /**
       *
       * 
       *
       * */
      openFullScreen() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 6000);
      },
      open4(info) {
        this.$message.error(info);
      },
      open2(info) {
        this.$message({
          message: info,
          type: 'success'
        });
      },
      /**
       *
       *
       *
       *
       *
       **/
      checked(pass, obj) {
        if (pass != "" && !obj.rule.test(pass)) {
          obj.msg = obj.msgError;
          return false;
        } else if (pass == "") {
          obj.msg = '*';
          obj.flag = false;
        } else {
          obj.msg = '';
          obj.flag = true;
        }
      },
      sumit() {
        let obj = this.checkedMsg;
        let flag = obj.docvalue.flag && obj.servalue.flag && obj.datevalue.flag;
        if (flag) {
          this.smitInfo = '';
          this.openFullScreen();
          this.order()
        } else {
          this.smitInfo = ''
        }
      },
      /**
       *
       *
       *
       * 
       *
       *
       * */
      order() {
        this.axios.post(this.api_url + 'Reservation/addReservation', {
          "cusId": this.$route.params.cusId,
          "doctorId": this.docvalue,
          "otherInfo": this.desc,
          "reservationServiceId": this.servalue,
          "reservationServiceTime": this.Dateformat(this.datevalue)
        }, {
          headers: {
            "Authorization": this.msg.data
          }
        })
        //
          .then(response => {
            // console.log(response.data);
            if (response.data.code === '0000') {
              this.fullscreenLoading = false;
              this.open2(response.data.msg);
              this.$router.push({path: '/appSuccess/' + this.$route.params.cusId});
            }/*else if(response.data.code==='0000'){

            }*/
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * token
       * */
      post() {
        this.axios.post(this.api_url + 'security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          "key": "",
          "requestTime": ""
        })
        //
          .then(response => {
            //console.log(response.data);
            this.msg = response.data;
            this.getService();
          })
          //
          .catch(error => {
            console.log(error)
          })
        /**
         *
         * 
         * */
      },

      Dateformat(date) {
        if (date != '') {
          let aa = new Date(date).getTime();
          aa = new Date(aa);
          let year = aa.getFullYear();
          let month = (Array(2).join('0') + (aa.getMonth() + 1)).slice(-2);
          let date = (Array(2).join('0') + aa.getDate()).slice(-2);
          let hours = (Array(2).join('0') + aa.getHours()).slice(-2);
          let minutes = (Array(2).join('0') + aa.getMinutes()).slice(-2);
          let seconds = (Array(2).join('0') + aa.getSeconds()).slice(-2);
          return [year, month, date].join('-') + ' ' + [hours, minutes, seconds].join(':');
        } else {
          return null
        }

        //return aa.getDate()
      },
      /**
       *
       * 
       * */
      getdoctor() {
        this.axios.post(this.api_url + 'Reservation/getDoctors/' + this.$route.params.cusId)
        //
          .then(response => {
            this.docmsg = response.data.data;
            for (let i = 0; i < this.docmsg.length; i++) {
              let {doctorId, doctorName} = this.docmsg[i];
              let obj = {value: doctorId, label: doctorName};
              //console.log(obj);
              this.options1.push(obj)
            }
            //console.log(response.data.data)
          })
          //
          .catch(error => {
            console.log(error)
          });
      },
      /**
       *
       *
       * 
       * */
      getService() {
        this.axios.post(this.api_url + 'Reservation/getService')
        //
          .then(response => {
            this.itemmsg = response.data.data;
            for (let i = 0; i < this.itemmsg.length; i++) {
              let {serviceName, serviceId} = this.itemmsg[i];
              let obj = {value: serviceId, label: serviceName};
              //console.log(obj);
              this.options.push(obj)
            }
            //console.log(response.data.data)
            this.getdoctor();

            //console.log(response1)
          })
          //
          .catch(error => {
            console.log(error)
          });
      }
    }
  }
</script>
<style lang="less">
  .el-input {
    .el-input__inner {
      border: 0;
      text-align: left;
      padding-right: 0;
      padding-left: 10px;
      margin-top: 1vh;
    }
  }
</style>
<style scoped>
  .smitinfo {
    position: absolute;
    color: red;
    left: 0;
    right: 0;
    text-align: center;
  }

  .checked {
    position: absolute;
    right: 20px;
    margin-top: 20px;
    color: red !important;
  }

  .el-row textarea {
    border: 0;
    height: 30vh;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
    font-size: 16px;
    color: rgba(110, 110, 110, 1);
    padding: 10px;
    width: 100%;
    outline: none;
  }

  .icon {
    text-align: center;
    margin-top: 2vh;
  }

  .icon img {
    width: 15vh;
  }

  .icon p {
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
    font-weight: 600;
    font-size: 2vh;
    color: #aaaaaa;
    margin-bottom: 2vh;
  }

  .el-row {
    margin: 0;
    border-bottom: 1px solid #cccccc;
    background-color: white;

  }

  .el-row:nth-child(2) {
    border-top: 1px solid #cccccc;
  }

  .el-col button {
    margin-top: 2vh;
  }

  .title {
    text-align: center;
  }

  .title img {
    margin-top: 8px;
    width: 5vh;
  }
</style>



