<template>
  <div v-loading.fullscreen.lock="fullscreenLoading">

    <div class="re-container">
      <div style="height: 40px"></div>
      <div class="icon">
        <img  src="../assets/img/logo-zhufu.png">
      </div>

      <div class="re-line">
        <img class="icons" src="../assets/img/icon-account.png">
        <input type="text" v-model="cusname"  @change="checkZmOrNum(cusname)" placeholder="/"><span class="checked">{{this.checkname}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-secret.png">
        <input  type="password" v-model="password" @change="checkPassword(password)" placeholder=""><span class="checked">{{this.checkpass}}</span>
      </div>
      <div class="btn">
        <button @click="sumit()">&nbsp &nbsp &nbsp &nbsp &nbsp</button>
        <div class="smitinfo">{{this.smitinfo}}</div>

      </div>

      <div class="re-more">
        <router-link :to="{path:'/Forgetsr/'+this.openid}" ></router-link>|<router-link :to="{path:'/Register/'+this.openid}" >></router-link>
      </div>
    </div>

  </div>
</template>
<script>
  import wx from 'weixin-js-sdk'

  var flag1=false;
  var flag2=false;
  export default {
    name: "Login",
    data(){
      return{
        fullscreenLoading: false,
        msg:'',
        cusname:this.$route.params.index,
        checkname:'',
        checkpass:'',
        smitinfo:'',
        password:'',
        openid:this.$route.params.openid
      }
    },
    mounted(){
      this.checkZmOrNum(this.cusname);
      this.checkPassword(this.password);

    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
    },
    created() {
      this.post();
    },
    methods: {
      open4(info) {
        this.$message.error(info);
      },
      open2(info) {
        this.$message({
          message: info,
          type: 'success'
        });
      },
      openFullScreen() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 2000);
      },
      post() {
        this.axios.post(this.api_url + '/security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      login(){
        this.post();
        console.log(this.msg.data);
        this.axios.post(this.api_url+ 'login/userLogin',{
          "openid": this.openid,
          "userAcc": this.cusname,
          "userPwd": this.password
        },{
          headers: {"Authorization": this.msg.data},
        })
        //
          .then(response => {
            console.log(response.data)
            console.log(this.openid)
            if (response.data.code === "0002") {

              this.post();
              this.login();

            } else if (response.data.code === "0001") {
              this.open4(response.data.msg)
              console.log('')
            }else {
              this.open2(response.data.msg)
              wx.miniProgram.navigateBack({
                delta: 2
              })
              console.log('')
            }

          })
          //
          .catch(error => {
            console.log(error)
          });
      },
      checkZmOrNum(zmnum) {
        var zmnumReg = /^[0-9a-zA-Z]*$/;
        if (zmnum != "" && !zmnumReg.test(zmnum)) {
          this.checkname='*';
          flag1=false;
          /*alert(",");*/
          return false;
        }else if(zmnum == ""){
          this.checkname='*';
          flag1=false;
        }else {
          this.checkname='';
          flag1=true;
        }
      },
      checkPassword(pass){
        var Reg = /^.{6,16}$/;
        if (pass != "" && !Reg.test(pass)) {
          this.checkpass='*6-16';
          flag2=false;
          /*alert(",");*/
          return false;
        }else if(pass == ""){
          this.checkpass='*';
          flag2=false;
        }else {
          this.checkpass='';
          flag2=true;
        }
      },
      sumit(){
        console.log(flag1&&flag2);
        if(flag1&&flag2){
          this.smitinfo='';
          this.openFullScreen();
          this.login();
        }else {
          this.smitinfo=''
        }
      }
    }
  }
</script>

<style scoped>
  .smitinfo{
    position: absolute;
    margin-top: 10px;
    color: red;
    left: 0;
    right: 0;
    text-align: center;
  }
  .checked{
    position: absolute;
    right: 20px;
    margin-top: 20px;
    color: red;
  }
  .re-bg-bottom{
    position: fixed;
    width: 100%;
    bottom: 0;
  }

  .re-container{
    height: 100vh;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .re-line{
    text-align: left;
    padding:5px 0;
    margin: 10px 20px;
    border-bottom: 1px solid #cccccc;
  }
  .icon{
    text-align: center;
    width: 100%;
    margin-top: -20vh;
    margin-bottom: 20px;
  }
  .icon img{
    width: 100px;
  }
  .icons{
    width: 25px;
    margin: 0 10px;
    position: relative;
    bottom: -8px;
  }
  input{
    margin-left: 10px;
    border: 0;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
  }
  input:focus { outline: none; }
  .btn{
    margin-top: 10vh;
    text-align: center;
  }
  .btn button{

    text-align: center;

    font-size: 18px;
    width: 60%;
    background: linear-gradient(to top,rgb(123,174,245), rgb(58,142,234) );
    border: 0;
    border-radius: 20px;
    height: 40px;
    color:#ffffff;

  }
  .btn button:active{
    border: 2px solid #ffffff;
    border-radius: 25px;
    font-size: 17px;
  }
  .btn button:hover{
    outline: none;
  }
  .re-more{
    margin-top: 10vh;
    color:#cccccc
  }
  .re-more a{
    font-size: 16px;
    margin: 0 6px;
    letter-spacing: 3px;
    text-decoration:none;
    color:#666666
  }
  .re-more a:hover{text-decoration:none;}
</style>



