<template>
  <div class="container" v-loading.fullscreen.lock="fullscreenLoading">
    <div class="card" v-for="(item,index) in this.serList.data">
      <div class="item1">
        <div class="Sername">{{item.name}}</div>
        <div class="Serdate">{{item.date}}</div>
      </div>
      <div class="item2">
         <span>{{item.days}}</span> 
      </div>
      <div class="item3">
      </div>
    </div>
    <!--<div class="null" id="null">
      <img src="../assets/img/wushuju.png" >
      <p></p>
    </div>-->
    <null v-show=this.flag></null>
    <div style="height: 40px"></div>

    <div class="header" id="header">
      <span @click="back"><img src="../assets/img/back.png"></span>
      <span @click="close"><img src="../assets/img/close.png"></span>
      <span @click="advance"><img src="../assets/img/advance.png"></span>
    </div>
  </div>
</template>

<script>
  import wx from 'weixin-js-sdk'
  import Null from '../components/null'

  export default {
    name: "healthSer",
    data() {
      return {
        flag: false,
        rep: '',
        menuList: '',
        Histories: '',
        serList: '',
        msg: '',
        fullscreenLoading: false
      }
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
      cusId() {
        return this.$store.state.cusId
      },
    },
    components: {
      null: Null
    },
    /*mounted(){
      if(this.serList.data.length===0){
        this.flag=true;
      }
    },*/
    created() {
      this.post();
    },
    methods: {
      /**
       *
       * 
       *
       * */

      getlist() {
        // document.getElementById('load').style.display='block';
        console.log(this.$route.params.cusId)
        this.openFullScreen();
        this.axios.post(this.api_url + 'service/getHealthServiceList/0', {
          "cusId": this.$route.params.cusId,
          "openid": "",
          headers: {'Authorization': this.msg.data}
        })
        //
          .then(response => {

            if (this.serList.code === "0002") {
              this.post();
              this.getlist();
            } else {
              this.serList = response.data;
              this.fullscreenLoading=false;
              if(this.serList.data.length===0){
                this.flag=true
              }
            }

          })
          //
          .catch(error => {
            console.log(error)
          });

      },
      openFullScreen() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 30000);
      },
      post() {
        this.axios.post(this.api_url + 'security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
            this.getlist();
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      close() {
        wx.miniProgram.navigateBack({
          delta: 2
        });
      },
      advance() {
        this.$router.go(1);
      },
      back() {
        this.$destroy('healthSer')
        this.$router.go(-1);

      },

    }
  }
</script>

<style scoped>

  .card {
    margin: 10px;
    display: flex;
    border: .5px solid #6296ca;
    flex-wrap: wrap;
    border-radius: 6px;
    background-color: white;
  }

  .item1 {
    flex: 0 0 50%;
    /*background-color: cornflowerblue;*/
    padding-left: 10px;
  }

  .item1 .Sername {
    display: flex;
    justify-content: flex-start;
    margin: 10px 0;
    font-size: 18px;
    font-weight: 600;
  }

  .item1 .Serdate {
    display: flex;
    justify-content: flex-start;

  }

  .item2 span {
    color: #6495ed;
    font-family: '';

  }

  .item2 {
    display: flex;
    flex: 0 0 50%;
    justify-content: flex-end;
    align-items: flex-end;
    padding-right: 10px;
  }

  .item3 {
    margin-top: 10px;
    border-radius: 5px;
    display: flex;
    min-height: 10px;
    min-width: 100%;
    background-color: #6495ed;
  }

</style>



