<template>
  <div class="container">
    <p class="title">{{content.presciptionName}}</p>
    <div class="content">
      <div  v-html="content.presciptionContent"></div>
      <p class="date">{{content.presciptionDate}}</p>
    </div>
    <div style="height: 60px"></div>
  </div>
</template>

<script>
  export default {
    name: "InterContent",
    data() {
      return {
        msg: '',
        content: '',
      }
    },
    created() {
      this.post()
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
    },
    methods: {
      post() {
        this.axios.post(this.api_url + 'security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {key: "", requestTime: ""})
        //
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
            this.getContent()
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * 
       *
       * */
      getContent() {
        console.log(this.msg.data)
        this.axios.get(this.api_url +'report/getPrescriptionContent/' + this.$route.params.presciptionId, {
          headers: {
            'Authorization': this.msg.data,
            'content-type': 'application/json'
          }
        })
        //
          .then(response => {
            console.log(response.data);
            this.content=response.data.data
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
    }
  }
</script>

<style scoped>
  .title{
    font-size: 18px;
    text-align: center;
    margin: 10px;
  }
  .content{
    padding: 10px;
    margin: 10px;
    box-shadow: 5px 5px 10px #cccccc;
    border: 1px rgba(114,114,114,.1) solid;
  }
  .date{
    text-align: right;
  }
</style>



