<template>
  <div v-loading.fullscreen.lock="fullscreenLoading">

    <div class="re-container">
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-phone.png">
        <input type="text" v-model="cusMobile" @keyup="checked(cusMobile,checkedMsg.cusMobile)"
               placeholder=""><span class="checked">{{this.checkedMsg.cusMobile.msg}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-yanzhengma.png">
        <input type="text" v-model="cusPhoneCode" placeholder=""
               @keyup="checked(cusPhoneCode,checkedMsg.cusPhoneCode)"><input class="span" type="button" id="btn"
                                                                             value="" @click="condsend()"
                                                                             onclick="settime(this)"/>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-secret.png">
        <input type="password" v-model="userPwd" @keyup="checked(userPwd,checkedMsg.userPwd)" placeholder=""><span
        class="checked">{{this.checkedMsg.userPwd.msg}}</span>
      </div>
      <div class="re-line">
        <img class="icons" src="../assets/img/icon-secret.png">
        <input type="password" v-model="userPwd1" @keyup="passcheck()"
               placeholder=""><span
        class="checked">{{this.checkedMsg.userPwd1.msg}}</span>
      </div>
      <div class="btn">
        <button @click="sumit()">&nbsp&nbsp&nbsp&nbsp</button>
        <div class="smitinfo">{{this.smitInfo}}</div>

      </div>
    </div>
  </div>
</template>

<script>
  var flag1 = false;
  var flag2 = false;
  export default {
    name: "Register",
    data() {
      return {
        fullscreenLoading:'',
        checkedMsg: {
          cusMobile: {msg: '*', flag: false, rule: /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/, msgError: ''},
          cusPhoneCode: {msg: '* ', flag: false, rule: /^.+$/, msgError: '*'},
          userPwd: {msg: '*', flag: false, rule: /^.{6,16}$/, msgError: '6-16'},
          userPwd1: {msg: '*', flag: false,  msgError: ''},
        },
        smitInfo: '',
        cusMobile: "",
        cusPhoneCode: "",
        userPwd: "",
        userPwd1: "",
        openid:this.$route.params.openid
      }
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
    },
    created() {
      this.post();
    },
    methods: {
      passcheck(){
        if(this.userPwd===this.userPwd1){
          this.checkedMsg.userPwd1.msg=''
          this.checkedMsg.userPwd1.flag=true
        }else {
          this.checkedMsg.userPwd1.msg=this.checkedMsg.userPwd1.msgError
        }
      },
      open4(info) {
        this.$message.error(info);
      },
      open2(info) {
        this.$message({
          message: info,
          type: 'success'
        });
      },
      openFullScreen() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 2000);
      },
      amend() {
        this.axios.post(this.api_url + 'register/resetPwd/' + this.cusMobile + '/' + this.cusPhoneCode + '/'+this.userPwd , {}, {
          headers: {"Authorization": this.msg.data},
        })
        //
          .then(response => {
            if (response.data.code === "0002") {
              this.post();
              this.regist();

            } else if (response.data.code === "0001") {
              this.open4(response.data.msg)
            }else {
              this.open2(response.data.msg)
              console.log('');
              this.$router.push({path:'/Login/'+this.cusMobile+'/'+this.openid});
            }

          })
          //
          .catch(error => {
            console.log(error)
          });
      },
      sumit() {
        let obj = this.checkedMsg;
        let flag = obj.cusMobile.flag && obj.userPwd.flag && obj.cusPhoneCode.flag&&obj.userPwd1.flag;
        console.log(flag);
        if (flag) {
          this.smitInfo = '';
          this.openFullScreen();
          this.amend();
        } else {
          this.smitInfo = ''
        }
      },
      checked(pass, obj) {
        if (pass != "" && !obj.rule.test(pass)) {
          obj.msg = obj.msgError;
          return false;
        } else if (pass == "") {
          obj.msg = '*';
          obj.flag = false;
        } else {
          obj.msg = '';
          obj.flag = true;
        }
      },
      post() {
        this.axios.post(this.api_url + '/security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      condsend() {
        this.axios.post(this.api_url + '/register/getPhoneCode/' + this.cusMobile + '/' + 1, {}, {
          headers: {"Authorization": this.msg.data},
        })
        //
          .then(response => {
            if(response.data.code=='0001'){
              this.open4(response.data.msg)
            }
            console.log(response.data)

          })
          //
          .catch(error => {
            console.log(error)
          });
      },
    }
  }
</script>

<style scoped>
  .smitinfo {
    position: absolute;
    margin-top: 10px;
    color: red;
    left: 0;
    right: 0;
    text-align: center;
  }

  .checked {
    position: absolute;
    right: 20px;
    margin-top: 20px;
    color: red;
  }

  .re-bg-bottom {
    position: absolute;
    width: 100%;
    bottom: 0;
  }

  .re-container {
    height: 100vh;
    width: 100%;
    text-align: center;
  }

  .re-line {
    text-align: left;
    padding: 5px 0;
    margin: 10px 20px;
    border-bottom: 1px solid #cccccc;
  }

  .re-line .span {
    position: absolute;
    right: 20px;
    background-color: white;
    color: rgb(123, 174, 245);
    font-size: 16px;
  }

  .icon {
    text-align: center;
    width: 100%;
    margin-top: -20vh;
    margin-bottom: 20px;
  }

  .icon img {
    width: 100px;
  }

  .icons {
    width: 25px;
    margin: 0 10px;
    position: relative;
    bottom: -8px;
  }

  input {
    margin-left: 10px;
    border: 0;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
  }

  input:focus {
    outline: none;
  }

  .btn {
    margin-top: 10vh;
    text-align: center;
  }

  .btn button {

    text-align: center;

    font-size: 18px;
    width: 60%;
    background: linear-gradient(to top, rgb(123, 174, 245), rgb(58, 142, 234));
    border: 0;
    border-radius: 20px;
    height: 40px;
    color: #ffffff;

  }

  .btn button:active {
    border: 2px solid #ffffff;
    border-radius: 25px;
    font-size: 17px;
  }

  .btn button:hover {
    outline: none;
  }

  .re-more {
    margin-top: 10vh;
    color: #cccccc
  }

  .re-more a {
    font-size: 16px;
    margin: 0 6px;
    letter-spacing: 3px;
    text-decoration: none;
    color: #666666
  }

  .re-more a:hover {
    text-decoration: none;
  }
</style>



