<template>
  <div class="content">
    <viewer :images="imgs">
      <img v-for="src in imgs" :src="src.url" :key="src.title">
    </viewer>
  </div>
  <!--<img :src=this.$store.state.repurl />-->

</template>

<script>
  import imageView from 'vue-imageview'

  export default {
    name: "reportConimg",
    components: {
      'image-view': imageView
    },
    data() {
      return {
        imgs: [
          {
            url: this.$store.state.repurl,
          },
        ]
      }
    }
  }
</script>

<style scoped>
  img{
    width: 100%;
  }
</style>



