        type="date"
        placeholder="" @change="getList">
      </el-date-picker>
      <div style="display: inline-block;margin: 0 10px"></div>
      <el-date-picker
        style="width: 100px"
        v-model="value2"
        type="date"
        placeholder="" @change="getList">
      </el-date-picker>
    </div>

  <!--  <p class="condition"></p>-->
    <transition-group tag="div">
      <div class="body-card condition" v-for="(item,key,index) in list.data"
           @click="openIntervention(item.presciptionId)" v-bind:key="key">
        <div class="title">{{item.presciptionName}}</div>
        <div class="content">{{item.presciptionDate}}</div>
      </div>
    </transition-group>
