    <null v-show=this.flag></null>
    <div style="height: 60px"></div>
  </div>

</template>

<script>
  import Null from './null'

  export default {
    name: "Intervention",
    data() {
      return {
        msg: '',
        list: '',
        flag: false,
        value1: '2018-03-21',
        value2: '2019-03-21',
      }
    },
    components: {
      null: Null
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
    },
    created() {
      this.post();
    },
    methods: {
      Dateformat(date){
        let aa=new Date(date).getTime();
        aa = new Date(aa);
        var year = aa.getFullYear();
        var month =(Array(2).join('0') + (aa.getMonth()+1)).slice(-2) ;
        var date =(Array(2).join('0') + aa.getDate()).slice(-2) ;
        return [year,month,date].join('-');
      },
      post() {
        this.axios.post(this.api_url + 'security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {key: "", requestTime: ""})
        //
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
            this.getList()
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * 
       *
       * */
      openIntervention(presciptionId) {
        this.$router.push({path: '/InterContent/' + presciptionId});
      },


      /**
       *
       * 
       *
       * */
      getList() {
        console.log(this.Dateformat(this.value1)+this.Dateformat(this.value2))
        this.axios.post(this.api_url +'report/getPrescription', {
          "cudId": this.$route.params.cusId,
          "startDate":this.Dateformat(this.value1),
          "endDate":this.Dateformat(this.value2),
          "templateDesc": "ZONGHEMUBAN",
        }, {

          headers: {
            'Authorization': this.msg.data,
            'content-type': 'application/json'
          }
        })
        //
          .then(response => {
            console.log(response.data);
            if (response.data.code === '0002') {
              this.post();
              this.getList()
            } else {
              this.list = response.data;
              if (this.list.data == '') {
                this.flag = true
              } else {
                this.flag = false
              }
            }
          })
          //
          .catch(error => {
            console.log(error)
          })
      }
    }
  }
</script>

