  .head-card {
    border: 1px dashed #000;
    margin: 5px;
    border-radius: 5px;
    box-shadow: 2px 5px 10px #cccccc;
  }

  .head-card img {
    width: 15px;
    margin-right: 5px;
    position: relative;
    bottom: -2px;
  }

  .head-item:first-child {
    border-bottom: 1px solid #000;
  }

  .head-item {
    display: flex;
    padding: 10px 0;
    margin: 0 10px;
  }

  .head-item div {
    flex: 1;
  }

  .head-item :nth-child(2) {
    text-align: right;
  }

  .body-card {
    height: 80px;
    line-height: 30px;
    border: 1px solid #cccccc;
    padding: 10px;
    margin: 10px 5px;
    border-radius: 5px;
    box-shadow: 2px 5px 10px #cccccc;
    border-right: 8px solid #6495ed;
  }

  .body-card .title {
    color: #333333;
    font-size: 17px;
    font-weight: 600;
  }

  .body-card .content {
    color: #999999;
    font-size: 14px;
    font-weight: 600;
  }
/*
  .condition {
    border-color: #6495ed;
    color: #6495ed;
  }

  .monitor {
    border-color: #3FD0E4;
    color: #3FD0E4;
  }

  .intervene {
    border-color: #FEAB24;
    color: #FEAB24;
  }*/

  p {
    margin-left: 10px;
    margin-top: 10px;
  }
</style>



