import Vue from 'vue'
import Router from 'vue-router'
import HealthReport from  '@/components/HealthReport'
import Menu from '@/components/Menu'
import Histories from '@/components/Histories'
import reportCon from '@/components/reportCon'
import healthSer from '@/components/healthSer'
import Register from '@/components/Register'
import Login from '@/components/Login'
import Forgetsr from '@/components/Forgetsr'
import Myinfo from '@/components/Myinfo'
import Article from '@/components/Article'
import reportConimg from '@/components/reportConimg'
import Intervention from '@/components/Intervention'
import InterContent from '@/components/InterContent'
import appointment from '@/components/appointment'
import Myappointment from '@/components/Myappointment'
import appSuccess from '@/components/appSuccess'



Vue.use(Router);


export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/Login'
    },
    {
      path: '/reportConimg',
      name:'/reportConimg',
      component:reportConimg
    },
    {
      path: '/Myappointment/:cusId',
      name:'/Myappointment',
      component:Myappointment
    },
    {
      path: '/appSuccess/:cusId',
      name:'/appSuccess',
      component:appSuccess
    },
    {
      path: '/reportConimg',
      name:'/reportConimg',
      component:reportConimg
    },
    {
      path: '/Forgetsr/:openid',
      name:'/Forgetsr',
      component:Forgetsr
    },
    {
      path: '/Article/:articleId',
      name:'/Article',
      component:Article
    },
    {
      path: '/Myinfo/:openid',
      name:'/Myinfo',
      component:Myinfo
    },
    {
      path: '/Login/:index/:openid',
      name:'/Login',
      component:Login
    },
    {
      path: '/InterContent/:presciptionId',
      name:'/InterContent',
      component:InterContent
    },
    {
      path: '/Register/:openid',
      name:'/Register',
      component:Register
    },
    {
      path: '/healthSer/:cusId',
      name:'/healthSer',
      component:healthSer
    },
    {
      path: '/reportCon',
      name:'/reportCon',
      component:reportCon
    },
    {
      path: '/HealthReport/:cusId',
      name:'/HealthReport',
      component:HealthReport
    },
    {
      path: '/Menu/:cusId',
      name:'/Menu',
      component:Menu
    },
    {
      path: '/Histories',
      name:'/Histories',
      component:Histories
    },
    {
      path: '/Intervention/:cusId',
      name:'/Intervention',
      component:Intervention
    },
    {
      path: '/appointment/:cusId',
      name:'/appointment',
      component:appointment
    },
  ]
})



