<template>
  <div >
      <h2 style="text-align: center">{{title}}</h2>
    <div class="content" >
      <div v-html="content"></div>
    </div>
  </div>
</template>

<script>

  export default {
    name: "Article",
    data() {
      return {
        msg:'',
        articleId:this.$route.params.articleId,
        content:'',
        title:''

      }
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
    },
    created() {
      this.post();
      this.getArticle();
    },
    methods: {

      back() {
        this.$router.go(-1);
      },
      openFullScreen() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 2000);
      },
      getArticle() {
        this.axios.post(this.api_url + 'Article/getBaikeContent/' + this.articleId, {}, {
          headers: {"Authorization": this.msg.data},
        })
        //
          .then(response => {
            console.log(response.data)
            if (response.data.code === "0002") {
              this.post();
              this.getArticle();

            } else{
              this.content=response.data.data.content;
              this.title=response.data.data.title
            }

          })
          //
          .catch(error => {
            console.log(error)
          });
      },
      post() {
        this.axios.post(this.api_url + '/security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
          })
          //
          .catch(error => {
            console.log(error)
          })
      },
    }
  }
</script>

<style scoped>
.content{
  line-height: 20px;
  padding: 10px;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
  font-weight: 600;
}
.content img{
  width: 100%;
}
</style>



